import jirakaiky from './images/jirakaiky.png';
import mvola from './images/mvola.png';
import airtel from './images/airtel.png';
import paositra from './images/paositra.png';
import bfv from './images/bfv-logo.png';
import './css/App.css';
import 'bootstrap/dist/css/bootstrap.min.css'
import 'bootstrap/dist/js/bootstrap.bundle.min.js';
import { Container, Form, Row, Col, Image, Modal, Button, Card, ListGroup } from 'react-bootstrap';
import LoadingButton from './js/LoadingButton';
import LoadingButton1 from './js/LoadingButton1';
import LoadingButton2 from './js/LoadingButton2';
import React, { useState, useEffect } from 'react';
import { fetchData } from './js/factureCtrl';

function App() {
  const [isButtonVisible, setButtonVisibility] = useState(false);
  const [areButtonsVisible, setButtonsVisibility] = useState(false);

  const handleLoadingComplete = () => {
    setButtonsVisibility(true);
  };

  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  const [currentPage, setCurrentPage] = useState(0);
  const [nombreitem, setNombreitem] = useState(338);
  const [filteredData, setFilteredData] = useState([]);
  const [inputRefFacture, setInputRefFacture] = useState('');
  const [displayedData, setDisplayedData] = useState(null);
  const [isChecking, setChecking] = useState(false);
  const [billStatus, setBillStatus] = useState('');
  const [invalidBill, setInvalidBill] = useState('');

  useEffect(() => {
    fetchDataAndUpdateState();
  }, [currentPage, nombreitem]);

  const fetchDataAndUpdateState = async () => {
    try {
      const result = await fetchData(currentPage, nombreitem);
      setFilteredData(result);
    } catch (error) {
      // Handle error if needed
    }
  };

  const handleButtonClick = async () => {
    try {
      setChecking(true);

      const result = await fetchData(currentPage, nombreitem);
      const foundData = result.find(item => item.refFacture === inputRefFacture);


      if (foundData) {
        setDisplayedData(foundData);
        setBillStatus('Facture payée');
      } else {
        setDisplayedData(null);
        setBillStatus('Facture impayée');
        setButtonVisibility(true);
        // Handle the case where the entered refFacture is not found
        setInvalidBill('Référence facture incorrecte ou facture impayée.');
      }
    } catch (error) {
      // Handle error if needed
    } finally {
      setChecking(false);
    }
  };

  // State to store the bill data
  return (
    <div className="App">
      <header className="App-header">
        <section>
          <Container className='general-container'>
            <Row className='row-responsive'>
              <Col className='col1-responsive'>
                <div className='container-image'>
                  <div className='text-block1'>
                    <Card className='card-style'>
                      <Card.Header>Facture</Card.Header>
                      <ListGroup variant="flush">
                        {displayedData && (
                          <>
                            <ListGroup.Item>Au nom de : {displayedData.nomClient}</ListGroup.Item>
                            <ListGroup.Item>Adresse : {displayedData.adresseClient}</ListGroup.Item>
                            <ListGroup.Item>Mois de : {displayedData.mois}</ListGroup.Item>
                            <ListGroup.Item>De l'année : {displayedData.annee}</ListGroup.Item>
                            <ListGroup.Item>Au montant de : {displayedData.montantFacture.toLocaleString()}Ar</ListGroup.Item>
                          </>
                        )}
                      </ListGroup>
                    </Card>
                  </div>
                  <div className='text-block2'>
                    <p className='p1'>
                      Statut de la facture : {billStatus}
                    </p>
                  </div>
                  {isButtonVisible && (
                    <div className='bouton-payement'>
                      <LoadingButton1 onLoadingComplete={handleLoadingComplete} />
                    </div>
                  )}
                  <br></br>
                  {areButtonsVisible && (
                    <Row>
                      <Col><button className='boutonTest' onClick={(e) => { e.preventDefault(); handleShow(); }}><Image src={mvola} className='logoTest' roundedCircle /></button></Col>
                      <Col><button className='airtel' onClick={(e) => { e.preventDefault(); handleShow(); }}><Image src={airtel} className='logoAirtel' roundedCircle /></button></Col>
                      <Col><button className='paositra' onClick={(e) => { e.preventDefault(); handleShow(); }}><Image src={paositra} className='logoPaositra' roundedCircle /></button></Col>
                      <Col><button className='boutonTest' onClick={(e) => { e.preventDefault(); handleShow(); }}><Image src={bfv} className='logoTest' roundedCircle /></button></Col>
                    </Row>
                  )}
                </div>
              </Col>
              <Col className='form-container col2-responsive'>
                <Form>
                  <Row>
                    <img className='jirakaiky-logo' src={jirakaiky} alt="" />
                  </Row>
                  <Form.Group>
                    <Row>
                      <p className='code-kiosk'>Code Kiosque</p>
                    </Row>
                    <Row>
                      <Col xs={2}></Col>
                      <Col><Form.Label htmlFor="refFactureInput">Référence facture</Form.Label>
                        <Form.Control type="text"
                          id="refFactureInput"
                          value={inputRefFacture}
                          onChange={e => setInputRefFacture(e.target.value)}></Form.Control></Col>
                      <Col xs={2}></Col>
                    </Row>
                    <Row>
                      <Col xs={2}></Col>
                      <Col><p className='invalid-bill-text'>{invalidBill}</p></Col>
                      <Col xs={2}></Col>
                    </Row>
                    <Row>
                      <Col xs={2}></Col>
                      <Col><LoadingButton onLoadingComplete={handleButtonClick} /></Col>
                      <Col xs={2}></Col>
                    </Row>
                    <br></br>
                    <Row>
                      <Col xs={2}></Col>
                      <Col><LoadingButton2 onLoadingComplete={handleButtonClick} /></Col>
                      <Col xs={2}></Col>
                    </Row>
                  </Form.Group>
                  <br></br>

                  <br></br>
                </Form>
              </Col>
            </Row>
          </Container>
        </section>
        <Modal show={show} onHide={handleClose}>
          <Modal.Header closeButton>
            <Modal.Title>Confirmation débit</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <Form>
              <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                <Form.Label>Numero de téléphone à débiter</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="+261 34 56 345 98"
                  autoFocus
                />
              </Form.Group>
            </Form>
          </Modal.Body>
          <Modal.Footer>
            <Button variant="secondary" onClick={handleClose}>
              Close
            </Button>
            <Button variant="primary" onClick={handleClose}>
              Save Changes
            </Button>
          </Modal.Footer>
        </Modal>
      </header>
    </div>
  );
}

export default App;
