export const fetchData = async (currentPage, nombreitem) => {
    const apiUrl = 'https://preprod.api.cashless.eqima.org/api/paiement_facture/getAll3';

    try {
        const response = await fetch(`${apiUrl}?numeropage=${currentPage}&nombreitem=${nombreitem}`);
        const data = await response.json();

        // Assuming you want to filter data based on refFacture
        const filteredValues = data.data.map(item => {
            const { refFacture, nomClient, adresseClient, mois, annee, montantFacture} = item;
            return {
                refFacture,
                nomClient,
                adresseClient,
                mois,
                annee,
                montantFacture,
                // include other properties you want in the filtered result
            };
        });

        return filteredValues;
    } catch (error) {
        console.error(error);
        throw error;
    }
};